import React from 'react'
import './card.css';

const Card = ({ hero }) => {
  return (
    <div className="card">
      <div className="container">        
        {/* <img src={`https://robohash.org/${user.id}?set=set2`} alt="monster" className="imageItem" /> */}
        <img src={`/images/hero${hero.id}.png`} alt="hero" className="imageItem" />
        <h3>{hero.name}</h3>
        <p>{hero.email}</p>
      </div> 
    </div>      
  )
}

export default Card
